<template>
  <div class="production">
    <p
      class="mixer"
      style="
        color: brown;
        background-color: thistle;
        width: auto;
        margin-left: 25px;
      "
    >
      Mixer recorder
    </p>
    <p
      class="please"
      style="color: brown; background-color: silver; width: auto"
    >
      Please keep the record paper tidy
    </p>
    <label>Search by Sugar Box:</label>
    <input id="sugarbox-filter" v-model="filters" />
    <button id="button" @click="filterBySugarBox">filter</button>
    <input id="sugarbox-filter" v-model="selectedTime" />

    <label>Search by savour:</label>
    <select id="savour-filter" v-model="selectedSavour">
      <option value="">ALL</option>
      <option value="original">Original</option>
      <option value="sugarfree">Sugar Free</option>
      <option value="blacksesame">Black Sesame</option>
      <option value="lowsugar">Low Sugar</option>
    </select>
    <button id="button" @click="filterSavour">filter</button>

    <button class="btnExcel" @click="exportToCsv('myTable.csv', data)">
      Export to Excel
    </button>
    <ul class="pagination">
      <table id="customers">
        <tr class="title">
          <th scope="col">BoxID</th>
          <th scope="col">Sauce cooking time</th>
          <th scope="col">Capacity 4L bag</th>
          <th scope="col">Bean soaker tank number</th>
          <th scope="col">Sugar box number</th>
          <th scope="col">Savour</th>
          <th scope="col">Cylinder discharge temperature</th>
          <th scope="col">Puree brix</th>
          <th scope="col">Sugar cylinder temperature</th>
          <th scope="col">Finished product storage cylinder temperature</th>
          <th scope="col">Product brix</th>
          <th scope="col">Colleagues record</th>
          <th scope="col">Remark</th>
          <th></th>
        </tr>
        <tbody>
          <tr v-for="(post, index) in UpdatedTable" :key="post.id">
            <td>{{ index + 1 }}</td>
            <td>
              {{
                new Date(post.Time).toLocaleDateString("en-US", {
                  day: "2-digit",
                  month: "2-digit",
                  year: "numeric",
                })
              }}
            </td>
            <td v-for="post2 in post.Capacity" :key="post2.id">
              {{ post2.value }}
            </td>
            <td>
              {{ post.soaker }}
            </td>
            <td>{{ post.Sugarbox }}</td>
            <td>{{ post.savour[0].value }}</td>
            <td>{{ post.discharge }}</td>
            <td>
              {{ post.pureebrix }}
            </td>
            <td>{{ post.cylinder }}</td>
            <td>{{ post.Finished }}</td>
            <td>{{ post.Productbriz }}</td>

            <td>{{ post.Colleagues }}</td>
            <td>{{ post.Remark }}</td>
            <td>
              <button id="button" @click="editRow(post._id)">Edit</button>
              &nbsp;
              <button
                id="button"
                @click="requestApiSoftDeleteDoc('SoftDeleteDoc', post._id)"
              >
                Delete
              </button>
            </td>
          </tr>
        </tbody>
      </table>
      <li class="pagination_item">
        <button id="button" @click="onClickFirstPage" :disabled="isInFirstPage">
          First
        </button>
      </li>

      <li class="pagination_item">
        <button
          id="button"
          @click="onClickPreviousPage"
          :disabled="isInFirstPage"
        >
          Previous
        </button>
      </li>

      <!-- Visible Buttons Start -->
      <li v-for="page in pages" class="pagination_item" :key="page">
        <button
          id="button"
          @click="onClickPage(page)"
          :disabled="page.isDisabled"
          :class="{ active: isPageActive(page) }"
        >
          {{ page }}
        </button>
      </li>

      <!-- Visible Buttons End -->

      <li class="pagination_item">
        <button id="button" @click="onClickNextPage" :disabled="isInLastPage">
          Next
        </button>
      </li>

      <li class="pagination_item">
        <button id="button" @click="onClickLastPage" :disabled="isInLastPage">
          Last
        </button>
      </li>
    </ul>

    <div>
      <input
        type="date"
        v-model="date"
        @change="updateDate"
        @click="filterDate"
      />
      <p>date: {{ formatDate }}</p>
    </div>
  </div>
</template>

<script>
import { computed, ref } from "vue";
export default {
  setup() {
    const date = ref(new Date());

    function updateDate(event) {
      date.value = new Date(event.target.value);
    }

    const formatDate = computed(() => {
      const options = {
        day: "2-digit",
        month: "2-digit",
        year: "numeric",
      };
      return date.value
        .toLocaleDateString("en-GB", options)
        .split("/")
        .join("/");
    });

    return {
      date,
      updateDate,
      formatDate,
    };
  },

  data() {
    return {
      currentPage: 1,
      maxVisibleButtons: 5,
      perPage: 10,
      data_table: [],
      currentPageCode: "production",
      data: [],
      filters: "",
      dataFilter: [],
      selectedSavour: "",
      selectedCapacity: "",
      selectedTime: "",
      selecteddate: "",
      dataTime: [],
    };
  },
  mounted() {
    this.requestApiGetDocs("GetDocs");
  },

  computed: {
    displayedItems() {
      let result = this.data_table.filter(
        (item) => item.Ng_SysIsDeleted !== true
      );
      console.log(result);
      const start = (this.currentPage - 1) * this.perPage;
      const end = start + this.perPage;
      return result.slice(start, end);
    },
    UpdatedTable() {
      if (
        this.dataFilter !== undefined &&
        this.dataFilter !== null &&
        this.dataFilter.length >= 1
      ) {
        console.log(1);
        return this.dataFilter;
      } else {
        console.log("test", this.displayedItems);
        return this.displayedItems;
      }
    },

    startPage() {
      // When on the first page
      if (this.currentPage === 1) {
        return 1;
      }

      // When on the last page
      if (this.currentPage === this.pages) {
        return this.pages - this.maxVisibleButtons;
      }

      // When inbetween
      return this.currentPage - 1;
    },
    pages() {
      let result = this.data_table.filter(
        (item) => item.Ng_SysIsDeleted !== true
      );
      return Math.ceil(result.length / this.perPage);
    },
    isInFirstPage() {
      return this.currentPage === 1;
    },
    isInLastPage() {
      return this.currentPage === this.pages;
    },
  },

  methods: {
    requestApiGetDocs: function (api) {
      var requestPara = {
        PageCode: this.currentPageCode,
        Size: 1000,
        Index: 0,
        Offset: 0,
      };
      this.callApi(api, requestPara).then((response) => {
        this.data_table = response?.data?.items;
      });
    },

    requestApiSoftDeleteDoc: function (api, id) {
      var requestPara = {
        PageCode: this.currentPageCode,
        DocId: id,
      };
      this.callApi(api, requestPara).then((data) => {
        var info = data;
        console.log(info);
      });
      this.requestApiGetDocs("GetDocs");
    },
    filterBySugarBox() {
      if (this.filters === "") {
        return (this.dataFilter = this.displayedItems);
        //console.log("test", this.displayedItems);
      } else {
        return (this.dataFilter = this.data_table.filter(
          (item) => parseInt(item.Sugarbox) === parseInt(this.filters)
        ));
      }
    },

    filterSavour() {
      if (this.selectedSavour === "") {
        return (this.dataFilter = this.displayedItems);
        //console.log("test", this.displayedItems);
      } else {
        this.dataFilter = this.displayedItems.filter(
          (item) => item.savour[0].key === this.selectedSavour
        );
        return (this.dataTime = this.dataFilter.filter(
          (item) => (item.Time = this.selectedTime)
        ));
      }
    },
    filterDate() {
      if (this.formatDate === "") {
        return (this.dataFilter = this.displayedItems);
        //console.log("test", this.displayedItems);
      } else {
        return (this.dataFilter = this.data_table.filter(
          (item) => item.Time === this.formatDate
        ));
      }
    },

    editRow: function (id) {
      location.href = `https://app-dev.digiqal.com/isvtrials/vtl8/form/${this.currentPageCode}/Edit/${id}?env=dev`;
    },

    onPageChange(page) {
      this.currentPage = page;
    },
    onClickFirstPage() {
      this.onPageChange(1);
    },
    onClickPreviousPage() {
      this.onPageChange(this.currentPage - 1);
    },
    onClickPage(page) {
      this.onPageChange(page);
    },
    onClickNextPage() {
      this.onPageChange(this.currentPage + 1);
    },
    onClickLastPage() {
      this.onPageChange(this.pages);
    },
    isPageActive(page) {
      return this.currentPage === page;
    },

    //Hàm xuất dữ liệu ra file CSV
    exportToCsv() {
      console.log(1);
      this.data = this.UpdatedTable.map((item) => {
        return {
          Time: new Date(item.Time).toLocaleDateString("en-US", {
            day: "2-digit",
            month: "2-digit",
            year: "numeric",
          }),
          Capacity: item.Capacity[0].value,
          soaker: item.soaker,
          Sugarbox: item.Sugarbox,
          savour: item.savour[0].value,
          discharge: item.discharge,
          pureebrix: item.pureebrix,
          cylinder: item.cylinder,
          Finished: item.Finished,
          Productbriz: item.Productbriz,
          Colleagues: item.Colleagues,
          Remark: item.Remark,
        };
      });
      let csvContent = "data:text/csv;charset=utf-8,";
      let rows = this.data;

      if (!Array.isArray(rows)) {
        console.error("Invalid data type for rows");
        return;
      }

      // Get the header row
      let headerRow = Object.keys(rows[0]).join(",");

      // Add the header row to the CSV content
      csvContent += headerRow + "\r\n";

      // Add each row of data to the CSV content
      rows.forEach((row) => {
        let values = Object.values(row).join(",");
        csvContent += values + "\r\n";
      });

      // Download the CSV file
      let encodedUri = encodeURI(csvContent);
      let link = document.createElement("a");
      link.setAttribute("href", encodedUri);
      link.setAttribute("download", "data.xlsx");
      document.body.appendChild(link); // Required for FF
      link.click();
    },

    //Sử dụng hàm xuất dữ liệu
  },
};
</script>
